<template> 
  <div class="container">

 <!-- logo -->
     <div class="logo">
        <!-- <img :src="this.logo" />  -->
        <div>{{appName}}</div>
    </div>
    <!-- <div class="sys-help">
      <i class="el-icon-question"></i>
    </div> -->
    <span class="tool-bar">
      
      <el-tooltip class="item" effect="light" content="系统帮助" placement="top">
     <span class="sys-help"><i class="el-icon-question" @click="$router.push('sysIntro')"></i></span>
    </el-tooltip>
  </el-tooltip>
      <!-- 用户信息 -->
      <el-dropdown class="user-info-dropdown" trigger="hover">
        <span class="el-dropdown-link"><img :src="this.userAvatar" /> 你好，{{this.userName}}</span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item  @click.native="viewEmpInfo">个人信息</el-dropdown-item>
          <el-dropdown-item>设置</el-dropdown-item>
          <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </span>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import mock from "@/mock/index"
import MenuTree from "@/components/MenuTree"
import Cookies from "js-cookie"
export default {
  computed:{
    ...mapState({
      appName: state=>state.app.appName
    })
  },
  data() {
    return {
      userName: "",
      userAvatar: "",
    };
  },
  methods: {
    selectNavBar(key, keyPath) {
      console.log(key, keyPath)
    },

    viewEmpInfo:function()
    {
      this.$router.push("/sys/empInfo") 
    },
    
   // 退出登录
    logout: function() {
      this.$confirm("确认退出吗?", "提示", {
        type: "warning"
      })
      .then(() => {
        console.log("用户退出登陆...")
        sessionStorage.removeItem("user")
        this.deleteCookie("token")
        this.$router.push("/login") 
        this.$api.login.logout().then((res) => {
          }).catch(function(res) {
        })
      })
      .catch(() => {})
    },
    // 删除cookie
    deleteCookie: function(name) { 
        Cookies.remove(name)
    }
  },
  created() {

    var user = sessionStorage.getItem("user");
    if (user) {
      this.userName = user;
      this.userAvatar = require("@/assets/user2.jpg");
      console.log(this.userName)
    }
  }
};
</script>

<style scoped lang="scss">
.container {
  position: absolute;
  left: 0px;
  right: 0px;
  height: 60px;
  line-height: 60px;
  background-color:rgba(30,133,240,0.8);
  .collapse-switcher {
    width: 40px;
    float: left;
    cursor: pointer;
    background-color:rgba(30,133,250,0.8);
    border-left-width: 1px;
    border-left-style: solid;
    border-right-width: 1px;
    border-right-style: solid;
  }
   .logo{
   position:absolute;
   top: 0px;
   height: 60px;   
   line-height: 60px;
  
   div {
     margin:10px;
     font-size: 30px;
     color: white;
     text-align: left;
     width:auto;
   }
 }
  .nav-bar {
    margin-left: auto;
    float: left;
    .el-menu {
      background: #504e6180;
    }
  }
  .tool-bar {
    float: right;
    
    .sys-help{
      margin:5px;
      height: 60px;
      width:60px;
      cursor: pointer;
      font-size: 25px;
       color: #fff;
    }
  
    .user-info-dropdown {
      font-size: 20px;
      padding-right: 20px;
      color: #fff;
      cursor: pointer;
      img {
        width: 40px;
        height: 40px;
        border-radius: 10px;
        margin: 10px 0px 10px 10px;
        float: right;
      }
    }
  }
}
</style>


