<template>
  <div class="login-container-temp">
  <div class="icon" :style="backImg">
  <el-form :model="loginForm" :rules="fieldRules" ref="loginForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="工号"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
 
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:25%;" @click.native.prevent="reset">重 置</el-button>
      <el-button type="primary" style="width:25%;" @click.native.prevent="login" :loading="logining">登 录</el-button>
    </el-form-item>
  </el-form>
</div>
  </div>
</template>

  <script>
  import mock from "@/mock/index"
  import { mapState } from 'vuex'
  import Cookies from "js-cookie"
  export default {
    name: 'Login',
    data() {
      return {
        userName:"",

        // bg
        backImg:{
          backgroundImage:"url("+require("@/assets/bg3.jpg")+")",
          backgroundRepeat:"no-repeat",
          backgroundSize: "cover",
          height: "100%",
          width: "100%",
          display :"flex"

        },
        logining: false,
        loginForm: {
          account: '',
          password: ''
        },
        fieldRules: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
          ]
        },
        checked: true
      };
    },
    methods: {
    	login() {
      this.loading = true
      let userInfo = {account:this.loginForm.account, password:this.loginForm.password}
       this.getUserName(userInfo.account);
       console.log(this.userName)
      this.$api.login.login(userInfo).then((res) => {
          if(res.msg != null) {
            this.$message({
              message: res.msg,
              type: 'error'
            })
          } else {
            console.log(res.data);
            Cookies.set('token', res.data.token) // 放置token到Cookie
            // 保存用户基本信息在sessionStorage
            // 保存用户到store中，方便后面条件查询
           sessionStorage.setItem('account',userInfo.account);
            sessionStorage.setItem('user',this.userName)
          
            this.$store.commit('menuRouteLoaded', false) // 要求重新加载导航菜单
            this.handleJumpPage(res.data.firstLoginFlag);
          }
          this.loading = false
        }).catch((res) => {
          this.$message({
          message: res.message,
          type: 'error'
          })
        });

    },

    // 登陆成功，处理页面跳转
    handleJumpPage:function(flag)
    {
      // 首次登陆
      if(flag===true)
      {
        // 修改原始密码
         this.$router.push('/updatePwd')
      }
      else
      {
        this.$router.push('/')
      }
    },
  
    // 获取账号对应姓名
    getUserName:function(account){
      this.$api.user.findUserByEno({'eno':account}).then(res => {
        if(res.msg != null) {
            this.$message({
              message: res.msg,
              type: 'error'
            })
          } else {
            console.log(res.data)
           this.userName = res.data.name 
         }})
    },
   
      reset() {
        this.$refs.loginForm.resetFields();
      }
    }
    
  }


</script>

<style lang="scss" scoped>
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background-color:rgba(182,255,255,0.5);
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


