import axios from '../axios'
/* 
 * 会议室预订接口模块
 */
//查询一周预约事件
export const findByRoom = (data) => {
    return axios({
        url: '/reserveInfo/findWeekInfos',
        method: 'post',
        data
    })
}


 // 保存
export const save = (data) => {
    return axios({
        url: '/reserveInfo/addReserve',
        method: 'post',
        data
    })
}
// 删除
export const batchDelete = (data) => {
    return axios({
        url: '/reserveInfo/delete',
        method: 'post',
        data
    })
}

// 修改
export const update = (data) => {
    return axios({
        url: '/reserveInfo/update',
        method: 'post',
        data
    })
}


