<template>
  <div class="login-container-temp">
  <div class="icon" :style="backImg">
  <el-form :model="updateForm" :rules="fieldRules" ref="updateForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">修改登录密码</h3>

    <el-form-item prop="oldPwd">
      初始密码：<el-input type="password" v-model="updateForm.oldPwd" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
 
    <el-form-item prop="newPwd">
      修改密码：<el-input type="password" v-model="updateForm.newPwd" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item prop="duplicatePwd">
      确认密码：<el-input type="password" v-model="updateForm.duplicatePwd" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:48%;" @click.native.prevent="reset">重 置</el-button>
      <el-button type="primary" style="width:48%;" @click.native.prevent="updateSubmit" :loading="logining">确认修改</el-button>
    </el-form-item>
  </el-form>
</div>
  </div>
</template>

  <script>
  import mock from "@/mock/index"
  import { mapState } from 'vuex'
  import Cookies from "js-cookie"
  export default {
    name: 'UpdatePwd',
    data() {
      return {
        // bg
        backImg:{
          backgroundImage:"url("+require("@/assets/bg3.jpg")+")",
          backgroundRepeat:"no-repeat",
          backgroundSize: "cover",
          height: "100%",
          width: "100%",
          display :"flex"

        },
        logining: false,
        updateForm: {
          oldPwd:"",
          newPwd:"",
          duplicatePwd:""
        },
        fieldRules: {
          oldPwd: [
            { required: true, message: '请输入初始', trigger: 'blur' },
          ],
           newPwd: [
            { required: true, message: '请输入新密码', trigger: 'blur' },
          ],
          duplicatePwd: [
            { required: true, message: '请输入确认密码', trigger: 'blur' },
          ]
        },
      };
    },
    methods: {

      updateSubmit:function()
      {
        this.loading = true
        // 检查两次密码是否一致
        if(this.updateForm.newPwd == this.updateForm.duplicatePwd)
        {
          var eno = sessionStorage.getItem('account')
          let updatePwd = {account:eno,password:this.updateForm.oldPwd,newPassword:this.updateForm.newPwd}
          console.log(updatePwd)
          this.$api.user.updatePassword(updatePwd).then((res) =>{
              if(res.code == 200) {
                this.$message({message: "更新密码成功",type: 'success'})
                this.$router.push("/")
              } else {
                this.$message({message: "操作失败："+res.msg,type: 'error'})
              }
          })
        }
        else
        {
           this.$message({message:'两次密码输入不一致，请重新输入',type: 'error' })
        }
      },

      reset() {
        this.$refs.updateForm.resetFields();
      }
    }
    
  }


</script>

<style lang="scss" scoped>
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background-color:rgba(182,255,255,0.5);
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


