import axios from '../axios'
/* 
 * 产量薪资计算模块
 */
//计算单价
export const getUnitPrice = (data) => {
    return axios({
        url: '/salaryCounter/getUnitPrice',
        method: 'post',
        data
    })
}
//计算计件薪资
export const getAmtSalary = (data) => {
    return axios({
        url: '/salaryCounter/getAmtSalary',
        method: 'post',
        data
    })
}

// 按时间查询日工资及班总产量
export const findAmtSalary = (data) => {
    return axios({
        url: '/salaryCounter/findAmtSalary',
        method: 'post',
        data
    })
}

// 导入日产量数据后一键计薪
export const calAmtSalary = (params) => {
    return axios({
        url: '/salaryCounter/calAmtSalary',
        method: 'get',
        params
    })
}



