import axios from '../axios'
/* 
 * 考勤模块
 */
//条件查询个人异常考勤数据
export const findDutyExcept = (data) => {
    return axios({
        url: '/dutyExcept/findDutyExcept',
        method: 'post',
        data
    })
}

//批量确认个人异常考勤数据
export const confirmDutyExcept = (data) => {
    return axios({
        url: '/dutyExcept/confirmDutyExcept',
        method: 'post',
        data
    })
}


//申请修改个人异常考勤数据 新增修改订单 askUpdateDuty
export const askUpdateDuty = (data) => {
    return axios({
        url: '/dutyExcept/addDutyOrder',
        method: 'post',
        data
    })
}

// 查找查询考勤数据修改申请单 findDutyOrders 
export const findDutyOrders = (data) => {
    return axios({
        url: '/dutyExcept/findDutyOrders',
        method: 'post',
        data
    })
}

// 新增异常考勤数据 addDutyExcept
export const addDutyExcept = (data) => {
    return axios({
        url: '/dutyExcept/addDutyExcept',
        method: 'post',
        data
    })
}



// 审批驳回 审批状态置-1 orderDeny
export const orderDeny = (data) => {
    return axios({
        url: '/dutyExcept/orderDeny',
        method: 'post',
        data
    })
}


// 批量审批 batchOrderConfirm
export const batchOrderConfirm = (data) => {
    return axios({
        url: '/dutyExcept/batchOrderConfirm',
        method: 'post',
        data
    })
}


