export default {
    state: {
        appName: "卷包车间信息查询平台",  // 应用名称
        menuRouteLoaded:false    // 菜单和路由是否已经加载
    },
   
    mutations: {
        menuRouteLoaded(state, menuRouteLoaded){  // 改变菜单和路由的加载状态
            state.menuRouteLoaded = menuRouteLoaded;
        }
    },
    actions: {
    }
}


