<template>
    <div class="menu-bar-container">
    <!-- logo -->
    <!-- <div class="logo" :class="isCollapse?'menu-bar-collapse-width':'menu-bar-width'"> -->
        <!-- <img :src="this.logo" />  -->
       <!--  <div>{{isCollapse?'':sysName}}</div>
    </div> -->
    <!-- 导航菜单 -->
    <el-menu ref="navmenu" default-active="1" @open="handleopen" @close="handleclose" @select="handleselect">
      <!-- 导航菜单树组件，动态加载菜单 -->
      <menu-tree v-for="item in navTree" :key="item.id" :menu="item"></menu-tree>
    </el-menu>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import mock from "@/mock/index"
import MenuTree from "@/components/MenuTree"

export default {
  components:{
        MenuTree
  },
  computed: {
    ...mapState({
      appName: state=>state.app.appName,
      navTree: state=>state.menu.navTree
    }),
     mainTabs: {
      get () { return this.$store.state.tab.mainTabs },
      set (val) { this.$store.commit('updateMainTabs', val) }
    },
    mainTabsActiveName: {
      get () { return this.$store.state.tab.mainTabsActiveName },
      set (val) { this.$store.commit('updateMainTabsActiveName', val) }
    }
  },
  watch: {
    $route: 'handleRoute'
  },
  data() {
    return {
      sysName: "",
      logo: "",
    };
  },
  methods: {
    handleopen() {
      console.log('handleopen');
    },
    handleclose() {
      console.log('handleclose');
    },
    handleselect(a, b) {
      console.log('handleselect');
    },
     // 路由操作处理
    handleRoute (route) {
      // tab标签页选中, 如果不存在则先添加
      var tab = this.mainTabs.filter(item => item.name === route.name)[0]
      if (!tab) {
        tab = {
          name: route.name,
          title: route.name,
          icon: route.meta.icon
        }
        this.mainTabs = this.mainTabs.concat(tab)
      }
      this.mainTabsActiveName = tab.name
      // 切换标签页时同步更新高亮菜单
      if(this.$refs.navmenu != null) {
        this.$refs.navmenu.activeIndex = '' + route.meta.index
        this.$refs.navmenu.initOpenedMenu()
      }
    }
  },
  created () {
    this.handleRoute(this.$route)
  },
};
</script>

<style scoped lang="scss">
.menu-bar-container {
  .el-menu {
    position:absolute;
    top: 60px;
    bottom: 0px;
    text-align: left;
  }
 /*  .logo {
   position:absolute;
   top: 0px;
   height: 60px;   
   line-height: 60px;
   background-color:rgba(30,133,240,0.8); 
   background: #4b5f6e;
  
   div {
     margin:10px;
     font-size: 24px;
     color: white;
     text-align: left;
     width:auto;
   }
 } */
  .menu-bar-width {
    width: 200px;
  }
  .menu-bar-collapse-width {
    width: 65px;
  }
}
</style>


