<template>
  <div class="page-container" style="width:99%;margin-top:15px;">
    <el-carousel :interval="3000" type="card" height="450px" class="carousel">
      <el-carousel-item class="carousel-item-intro">
        <h2>系统介绍</h2>
        <ul>
          <li>卷包信息查询平台</li>
          <li>旨在提供一套简洁易用的信息化查询系统</li>
          <li>方便查询机台产量、个人产量工资</li>
          <li>易于管理和确认考勤数据、顶岗信息</li>
        </ul>
        <div><img src="@/assets/logo_kitty_blue.png" style="width:200px;height:120px;padding-top:15px;" /></div>
      </el-carousel-item>
      <el-carousel-item class="carousel-item-func">
        <h2>功能介绍</h2>
        <ul>
          <li>✔ 薪资查询：日产量薪资查询、汇总薪资查询</li>
          <li>✔ 考勤管理：考勤查询、新增、修改，导出，异常考勤反馈</li>
          <li>✔ 顶岗管理：岗位变动查询、新增、修改，异常顶岗反馈</li>
          <li>✔ 日产量管理：机台日产量上传、查询</li>
          <li>✔ 工资管理：月工时调整、定额管理</li>
          <li>✔ 系统管理：用户管理、角色管理、菜单管理</li>
          <li>后续不断完善,扩展....</li>
          <li>...</li>
        </ul>
      </el-carousel-item>
      <el-carousel-item class="carousel-item-env">
        <h2>使用说明</h2>
        <ul>
          <li>机台人员当日可实时查询相应机台、牌号产量薪资</li>
          <li>汇总薪资模块，依赖当日机台产量数据，统计员导入系统后方能查询</li>
          <li>当前薪资涉及多个模块，多种场景，仅作为产量计薪参考</li>
          <li>请您按时核验顶岗、和考勤数据，若数据无误请确认</li>
          <li>若数据异常，请提交异常反馈，以便能及时修改</li>
          <li>请您多提宝贵意见，让我们的系统不断完善，更好的为大家服务</li>
        </ul>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
  export default {
    components:{
    },
    methods :{
    }
  }
</script>

<style>
  .carousel {
    padding-left: 20px;
    padding-right: 20px;
    margin-right: 20px;
  }
  .carousel h2 {
    color: #475669;
    font-size: 20px;
    opacity: 1.75;
    line-height: 100px;
    margin-left: 10px;
  }
  .carousel ul {
    color: #475669;
    font-size: 15px;
    opacity: 1.75;
    line-height: 40px;
    margin: 10px;
  }
  .carousel-item-intro h2 {
    color: #ffffff;
    font-size: 30px;
    opacity: 1.75;
    line-height: 80px;
    margin: 10px;
  }
  .carousel-item-intro ul {
    color: #ffffff;
    font-size: 20px;
    opacity: 1.75;
    line-height: 65px;
    margin-left: 30px;
  }
   .carousel-item-func h2 {
    color: #3f393b;
    font-size: 30px;
    opacity: 1.75;
    line-height: 50px;
    margin: 10px;
  }
  .carousel-item-func ul {
    color: #3f393b;
    font-size: 18px;
    opacity: 1.75;
    line-height: 40px;
    text-align: left;
    padding: 5px;
    padding-left: 90px;
    margin: 0;
  }
  .carousel-item-env h2 {
    color: #475669;
    font-size: 30px;
    opacity: 1.75;
    line-height: 50px;
    margin: 10px;
  }
  .carousel-item-env ul {
    color: #475669;
    font-size: 20px;
    opacity: 1.75;
    line-height: 35px;
    text-align: left;
    padding-left: 110px;
    margin: 10px;
  }
  .carousel-item-intro {
    background-color: #19aaaf73;
    -webkit-border-radius: 25px;
    border-radius: 25px;
    -moz-border-radius: 15px;
    background-clip: padding-box;
    box-shadow: 0 0 25px #a3b3b965;
  }
  .carousel-item-func {
    background-color: #19aaaf73;
    -webkit-border-radius: 25px;
    border-radius: 25px;
    -moz-border-radius: 15px;
    background-clip: padding-box;
    box-shadow: 0 0 25px #a3b3b965;
  }
  .carousel-item-env {
    background-color: #19aaaf73;
    -webkit-border-radius: 25px;
    border-radius: 25px;
    -moz-border-radius: 15px;
    background-clip: padding-box;
    box-shadow: 0 0 25px #a3b3b965;
  }
  .carousel-item-intro {
    background-color: #b95e5e;
  }
  .carousel-item-func {
    background-color: #52c578;
  }
  .carousel-item-env {
    background-color: #41a7b9;
  }
</style>


