import axios from '../axios'

/* 
 * 用户管理模块
 */
// 根据eno工号查询用户
export const findUserByEno = (params) => {
    return axios({
        url: '/user/findByEno',
        method: 'get',
        params
    })
}

// 保存
export const save = (data) => {
    return axios({
        url: '/user/save',
        method: 'post',
        data
    })
}
// 删除
export const batchDelete = (data) => {
    return axios({
        url: '/user/delete',
        method: 'post',
        data
    })
}
// 分页查询
export const findPage = (data) => {
    return axios({
        url: '/user/findPage',
        method: 'post',
        data
    })
}
// 查找用户的菜单权限标识集合
export const findPermissions = (params) => {
    return axios({
        url: '/user/findPermissions',
        method: 'get',
        params
    })
}

// 修改用户密码
export const updatePassword = (data) => {
    return axios({
        url: '/user/updatePassword',
        method: 'post',
        data
    })
}


