import axios from '../axios'
/* 
 * 员工岗位基本信息模块
 */
//查询所有员工基本信息
export const findEmpInfos= () => {
    return axios({
        url: '/empPostInfo/findEmpInfos',
        method: 'get',
    })
}

//条件查询员工基本信息
export const findEmpByName= (params) => {
    return axios({
        url: '/empPostInfo/findEmpByName',
        method: 'get',
        params
    })
}

//下拉项 查询当月排班表所有的操作工信息findForPull
export const findForPull= (params) => {
    return axios({
        url: '/empPostInfo/findForPull',
        method: 'get',
        params
    })
}


