import axios from '../axios'
/* 
 * 基本岗位模块
 */
//查询基本岗位数据
export const findAll = () => {
    return axios({
        url: '/basicPosition/findAll',
        method: 'get',
    })
}
//查询操作岗 基本岗位数据
export const findAllOperator = () => {
    return axios({
        url: '/basicPosition/findAllOperator',
        method: 'get',
    })
}


