import axios from '../axios'
/* 
 * 月工作时间点值管理模块
 */
//查询全部
export const findAll = (params) => {
    return axios({
        url: '/worktime/findAll',
        method: 'get',
        params
    })
}

//更新数据
export const updateWorkTime = (data) => {
    return axios({
        url: '/worktime/updateMonth',
        method: 'post',
        data
    })
}



