import axios from '../axios'
/**
 * 文件上传下载模块 
 * 日产量数据上传下载
 * 月工资明细上传下载
 */
export const downDayAmtTmp = (data) => {
    return axios({
        url: '/cigOutputInfo/downDayAmtTmp',
        method: 'post',
        data
    })
}


export const upDayAmtExecl = (data) => {
    return axios({
        url: '/cigOutputInfo/upDayAmtExecl',
        method: 'post',
        data
    })
}

export const downMonthSalaryTmp = (data) => {
    return axios({
        url: '/cigOutputInfo/downMonthSalaryTmp',
        method: 'post',
        data
    })
}


export const upMonthSalary = (data) => {
    return axios({
        url: '/fileUpDown/upMonthSalary',
        method: 'post',
        data
    })
}


