import axios from '../axios'
/* 
 * 岗位模块
 */
//条件查询个人当月顶岗数据
export const findPostChange = (data) => {
    return axios({
        url: '/position/findPostChange',
        method: 'post',
        data
    })
}

//批量确认个人顶岗数据
export const confirmPostExcept = (data) => {
    return axios({
        url: '/position/confirmPostExcept',
        method: 'post',
        data
    })
}


//申请修改个人顶岗数据 新增修改订单
export const askUpdatePost = (data) => {
    return axios({
        url: '/position/askUpdatePost',
        method: 'post',
        data
    })
}

// 查询顶岗修改数据修改单 findPostOrders
export const findPostOrders = (data) => {
    return axios({
        url: '/position/findPostOrders',
        method: 'post',
        data
    })
}

// 新增顶岗数据addEmpPost
export const addEmpPost = (data) => {
    return axios({
        url: '/position/addEmpPost',
        method: 'post',
        data
    })
}

// 批量 batchOrderConfirm
export const batchOrderConfirm = (data) => {
    return axios({
        url: '/position/batchOrderConfirm',
        method: 'post',
        data
    })
}


// 订单驳回
export const orderDeny = (data) => {
    return axios({
        url: '/position/orderDeny',
        method: 'post',
        data
    })
}


